@use "global" as *;

.sub-single-work-content-item {
    position: relative;
    padding-top: rem(22);
}

.sub-single-work-content-item__title {
    font-size: rem(20);
    font-weight: normal;
    letter-spacing: 0.02em;
    background: $black;
    padding: rem(8) rem(24);
    border: 1px solid $white;
    border-radius: 100vh;
    display: inline-block;
    position: absolute;
    left: 7%;
    transform: translateY(-50%);
    z-index: map-get($layer, "default" );
}

.sub-single-work-content-item__text {
    letter-spacing: 0.025em;
    line-height: 2.1875;
    border: 1px solid $white;
    padding: rem(56) rem(10) rem(33);
    position: relative;
    z-index: map-get($layer, "default" ) - 1;
    
    @include mq('md') {
        padding: rem(44) rem(22) rem(32);
    }

}