@use "global" as *;

.sub-single-cards {
    @include mq('md') {
        display: flex;
        flex-wrap: wrap;
    }
}

.sub-single-cards__item {
    margin-bottom: rem(16);
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    @include mq('md') {
        width: calc( 25% - rem(32) * 3 / 4);
        // margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    }
}

// .sub-single-cards__item:last-child {
//     margin-bottom: 0;
// }

.sub-single-cards__item:not(:nth-child( 4n )) {
    @include mq('md') {
        margin-right: rem(32);
    }
}

