@use "global" as *;

.sub-blog-content {
    
}

.sub-blog-content__inner {
    width: 100%;
    max-width: rem(600);
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    
    @include mq("md") {
      max-width: $inner + strip-unit($padding-pc) * 2;
      padding-right: $padding-pc;
      padding-left: $padding-pc;
    }
}

.sub-blog-content__category {
    margin-bottom: rem(40);
}

.sub-blog-content__cards {
    margin-bottom: rem(57);
    
    @include mq('md') {
        margin-bottom: rem(105);
    }

}


