@use "global" as *;

.sub-category {
    
}

.sub-category__lists {
    display: flex;
    flex-wrap: wrap;
}

.sub-category__list {
    margin-top: rem(12);
        
    @include mq('md') {
        margin-top: rem(0);
    }
}

.sub-category__list a {
    display: inline-block;
    transition: all 0.3s ease;
}

.sub-category__list:hover a {
    background: $white;
    color: #000000;
}

.sub-category__list:first-child {
    margin-right: rem(20);
            
    @include mq('md') {
        margin-right: rem(37);                
    }
}

.sub-category__list:nth-child(2) {
    margin-right: rem(16);

    @include mq('md') {
        margin-right: rem(34);                
    }
}

.sub-category__list:nth-child(3) {
    @include mq('md') {
        margin-right: rem(34);                
    }
}

.sub-category__list a {
    color: $white;
    letter-spacing: 0.125em;
    display: inline-block;
    padding: rem(2) rem(2) rem(4);
}

