@use "global" as *;

.to-top {
    width: rem(45);
    height: rem(45);
    position: fixed;
    bottom: rem(49);
    right: rem(24);
    background: $white;
    z-index: map-get($layer, floating);
    display: none;
    transition: all .3s ease;

    @include mq('md') {
        bottom: rem(32);
        right: rem(35);
    }
}

.to-top:hover {
    background: $gray;
}

.to-top:hover::before {
    background: url(/wp-content/themes/test/images/common/to-top-hover.png) no-repeat center center / cover;
}

.to-top::before {
    content: "";
    width: rem(24);
    height: rem(12);
    background: url(/wp-content/themes/test/images/common/to-top.png) no-repeat center center / cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}