@use "global" as *;

.sub-inquiry-content {
    color: $white;
}

.sub-inquiry-content__text {
    font-size: rem(20);
    text-align: center;
    margin-bottom: rem(80);
    letter-spacing: 0.1em;
    font-weight: normal;
    
    @include mq('md') {
        font-size: rem(30);
        letter-spacing: 0.0666em;
        line-height: 1.1666;
    }

}

.sub-inquiry-content__button {
    text-align: center;
}