@use "global" as *;

@keyframes zoomUp {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.15);
    }
}

.main-visual {
    color: $white;
    text-align: center;
    position: relative;
    z-index: map-get($layer , "default") + 1;    
}

.swiper-slide-active .main-visual__content,
.swiper-slide-duplicate-active .main-visual__content,
.swiper-slide-prev .main-visual__content{
  animation: zoomUp 10s linear 0s 1 normal both;  
}

.main-visual__content {
    height: 100vh;
    position: relative;
}

.main-visual__content--first {
    background: url(/wp-content/themes/test/images/top/topview-sp01.png) no-repeat center center / cover;
    // background: url(../../../../images/top/topview-sp01.png) no-repeat center center / cover;
    
    @include mq("md") {
        background: url(/wp-content/themes/test/images/top/topview-pc01.png) no-repeat center center / cover;
        // background: url(../../../../images/top/topview-pc01.png) no-repeat center center / cover;
    }
}

.main-visual__content--second {
    background: url(/wp-content/themes/test/images/top/topview-sp02.png) no-repeat center center / cover;
    
    @include mq("md") {
        background: url(/wp-content/themes/test/images/top/topview-pc02.png) no-repeat center center / cover;
    }
}

.main-visual__content--third {
    background: url(/wp-content/themes/test/images/top/topview-sp03.png) no-repeat center center / cover;
    
    @include mq("md") {
        background: url(/wp-content/themes/test/images/top/topview-pc03.png) no-repeat center center / cover;
    }
}

.main-visual__titles {
    position: absolute;
    width: 100%;
    left: 50%;
    top: 44.5%;
    transform: translateX(-50%);
    z-index: map-get($layer , "default") + 1;
    
    @include mq("md") {
        top: 42.5%;
        left: 50%;
    }
}

.main-visual__title {
    width: 100%;
    font-size: rem(26);
    font-weight: bold;
    letter-spacing: 0.1em;
    margin-bottom: rem(14);
    
    @include mq("md") {
        font-size: rem(50);
        letter-spacing: 0.01em;
        margin-bottom: rem(8);
    }
}

.main-visual__subtitle {
    position: absolute;
    width: 100%;
    font-size: rem(20);
    font-weight: bold;
    letter-spacing: 0.025em;
    
    @include mq("md") {
        font-size: rem(25);
        letter-spacing: 0.01em;
    }
}