@use "global" as *;

.contact-form {
    color: $white;
}

.contact-form__dl {
    @include mq('md') {
        padding: 0 rem(158) 0 0;
    }
}

.contact-form__row {
    margin-bottom: rem(32);
    
    @include mq('md') {
        display: flex;
    }
}

.contact-form__label {
    font-size: rem(14);
    letter-spacing: 0.05em;
    line-height: 1.71428;
    margin-bottom: rem(16);
    color: $white;
    
    @include mq('md') {
        max-width:rem(133);
        width: 100%;
        margin-right: rem(24);
        margin-bottom: 0;
        text-align: right;
        flex-grow: 0;
        line-height: 1.85;
    }
}

.contact-form__label.contact-form__label--center {
    @include mq('md') {
        line-height: rem(40);
    }
}

.contact-form__label label {

}

.contact-form__input {
    @include mq('md') {
        flex-grow: 1;
    }
}

.contact-form__input [type="text"] {
    box-shadow: none;
    border: 1px solid $white;
    background: $white;
    padding: rem(9);
    appearance: none;
    border-radius: o;
    color: inherit;
    font-family: inherit;
    font-size: rem(14);
    width: 100%;
}

.contact-form__input.contact-form__input--error [type="text"] {
    background: #FFDCDC;
    border: 1px solid #FFDCDC;
}


.contact-form__input [type="email"] {
    box-shadow: none;
    border: 1px solid $white;
    background: $white;
    padding: rem(9);
    appearance: none;
    border-radius: o;
    color: inherit;
    font-family: inherit;
    font-size: rem(14);
    width: 100%;
}

.contact-form__input.contact-form__input--error [type="email"] {
    background: #FFDCDC;
    border: 1px solid #FFDCDC;
}

.contact-form__input textarea {
    height: rem(400);
    box-shadow: none;
    border: 1px solid $white;
    background: $white;
    padding: rem(9);
    appearance: none;
    border-radius: o;
    color: inherit;
    font-family: inherit;
    font-size: rem(14);
    width: 100%;
    resize: none;

}

.contact-form__input.contact-form__input--error textarea {
    background: #FFDCDC;
    border: 1px solid #FFDCDC;
}

.contact-form__radio {
    text-align: center;
    margin-bottom: rem(16);
    
    @include mq('md') {
        margin-bottom: rem(40);
        
    }
}

.contact-form__radio label span {
    display: inline-block;
    padding-left: rem(26);
    position: relative;
    margin-right: rem(10);
}

.contact-form__radio span {
    color: $white;
    width: 100%;
    display: flex;

    @include mq('md') {
        display: inline-block;
        width: auto;
    }
}



.contact-form__radio  label span::before {
    content: "";
    width: rem(20);
    height: rem(20);
    border: 1px solid $white;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background: $white;
    border-radius: 50%;

}

.contact-form__radio label span::after {
    content: "";
    width: rem(10);
    height: rem(10);
    position: absolute;
    left: rem(5);
    top: 50%;
    transform: translateY(-50%);
    background: $black;
    border-radius: 50%;
    display: none;
}



.contact-form__radio [type="radio"] {
    display: none;
}

.contact-form__radio [type="radio"]:checked + span::after {
    display: block;
}

.contact-form__check {
    margin-bottom: rem(16);
    text-align: center;

    @include mq('md') {
        margin-bottom: rem(40);
        
    }
}

.contact-form__check span{
    color: $white;
}

.contact-form__check label span{
    display: inline-block;
    padding-left: rem(30);
    position: relative;
}

