}

.contact-form__check [type="checkbox"] {
    display: none;
}

.contact-form__button {
    text-align: center;
}

.contact-form__button [type="submit"] {
    background: $white;
    max-width: rem(360);
    width: 100%;
    display: inline-block;
    color: $gray;
    padding: rem(10) rem(146);
    appearance: none;
    font-family: inherit;
    border: none;
    font-size: rem(20);
    letter-spacing: 0.1em;
}


.contact-form__error {
    color: #FF5757;
    letter-spacing: 0.125em;
    text-align: center;
    margin-bottom: rem(32);
    
    @include mq('md') {
        margin-bottom: rem(30);
        
    }
}

// contact form 7のラジオボタンカスタマイズ
input [type="radio"] {
    display: none;
    position: absolute;
}

.wpcf7-list-item-label:before {

}



// contact form 7のエラーメッセージカスタマイズ
.wpcf7-validation-errors,
.wpcf7-acceptance-missing {
    background: none;
    border: none;
    padding:10px;
    color: #FF5757;
    letter-spacing: 0.125em;
    text-align: center;
}

.wpcf7 form.invalid .wpcf7-response-output, .wpcf7 form.unaccepted .wpcf7-response-output, .wpcf7 form.payment-required .wpcf7-response-output {
    background: none;
    border: none;
    padding:10px;
    color: #FF5757;
    letter-spacing: 0.125em;
    text-align: center;
}

.wpcf7 .wpcf7-not-valid {
    background: #FFDCDC;
    border: 1px solid #FFDCDC;
}

// ボタン位置の調整
.wpcf7-spinner {
    display: none;
}

// 送信完了後フォームを消す
.sent .contact-form__send-completely {
    display: none;
}

