@use "global" as *;

.button {
    display: block;
    color: $white;
    border: rem(1) solid $white;
    background: #333333;
    font-size: rem(20);
    letter-spacing: 0.1em;
    padding: rem(9) 0;
    text-align: center;
    transition: all .3s ease;
    position: relative;
    

    @include mq('md') {
        display: inline-block;
        font-size: rem(16);
        padding: rem(16) rem(136);
    }
}

.button.button--announce {
    
    @include mq('md') {
        font-size: rem(16);
        letter-spacing: 0;
        color: $black;
        background: $white;
        padding: rem(3) rem(26) rem(3) rem(29);
        flex-shrink: 0;
        }
}
    
.button.button--announce:hover {
        color: $white;
        border-color: $gray;
}
    
.button.button--announce::before {
    background: $gray;
}

.button::before {
    content: "";
        height: 100%;
        width: 100%;
        background: $white;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        transform: scaleX(0);
        transform-origin: 0% 50%;
        transition: all .3s ease;
        z-index: - 1;
}

.button:hover {
    color: $black;
    background: transparent;
}

.button:hover:before {
    transform: scaleX(1.0);
}

.button.button--pc-width-block {
    @include mq('md') {
        display: block;
    }
}