@use "global" as *;

.overview {
    position: relative;
}

//
.overview::before {
    content: "";
    width: 70vw;
    height: calc(100% - rem(131));
    background: $gray;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: map-get($layer , "default");
}

.overview__inner {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-right: auto;
    margin-left: auto;

    @include mq('md') {
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.overview__wrapper {     
    @include mq('md') {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row-reverse;
        padding: rem(81) 0 rem(82);
        position: relative;
        z-index: map-get($layer , "default");
    }
}

.overview__titles {
    margin-bottom: rem(70);
    position: relative;
    z-index: map-get($layer, "default" );
    
    @include mq('md') {
        margin-bottom: rem(-35);
    }

}

.overview__main-title.top-titles__main {
    @include mq('md') {
        top: rem(54);
    }
}

.overview__secondary-title {
        
    &.top-titles__secondary {
        @include mq('md') {
           letter-spacing: 0.03em;
        }
    }
    
}

.overview__image {
    background: $gray;
    position: relative;
    z-index: map-get($layer, "default" );

    

    @include mq('md') {
        width: rem(557);
        background: none;
        flex-shrink: 0;
    }

}

.overview__image img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}

.overview__image::before {
    content: "";
    display: block;
    padding-top: 69.6%;

    @include mq('md') {
        padding-top: 69.8%;
    }
}

.overview__body {
    background: $gray;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    padding-top: rem(24);
    padding-bottom: rem(20);
    position: relative;
    z-index: map-get($layer, "default" );
    
    @include mq('md') {
        background: none;
        margin-right: rem(33);
        padding-left: 0;
        padding-right: 0;
        padding-top: 0;
        padding-bottom: rem(8);
        z-index: map-get($layer , "default");
    }
}

.overview__diagonal-line {
    height: rem(1);
    width: 386%;
    background: $yellow;
    transform: rotate(105deg);
    transform-origin: 0% 0%;
    position: absolute;
    top: -10%;
    left: 63%;
    z-index: map-get($layer, "default" ) - 1;
    
    @include mq('md') {
        width: 142vw;
        transform: rotate(135deg);
        top: -31.5%;
        left: 100%;
        
    }
}

.overview__button {

    @include mq('md') {
        transition: all .3s ease;
        transform-origin: 0% 50%;
    }
}