@use "global" as *;

.sub-works-content {

}

.sub-works-content__category {
    margin-bottom: rem(17);
    
    @include mq('md') {
        margin-bottom: rem(40);
    }
}

.sub-works-content__inner {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    z-index: map-get($layer , "default");

    @include mq('md') {
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.sub-works-content__cards {
    margin-bottom: rem(81);
    
    @include mq('md') {
        margin-bottom: rem(104);
    }
}

