@use "global" as *;

.drawer {
    
}

.drawer__items {
    width: rem(26);
    height: rem(25);
    position: relative;
}

.drawer__item1,.drawer__item2,.drawer__item3 {
    position: absolute;
    width: rem(26);
    height: rem(2);
    background: $white;
}

.drawer__item1 {
    top: 0;
    left: 0;
}

.drawer__item2 {
    top: rem(6);
    left: 0;
}

.drawer__item3 {
    top: rem(12);
    left: 0;
}

.drawer.is-active .drawer__item1 {
    transform: rotate(-45deg);
    top: rem(8);
}

.drawer.is-active .drawer__item2 {
    display: none;
}

.drawer.is-active .drawer__item3 {
    transform: rotate(45deg);
    top: rem(8);
}