@use "global" as *;

.sub-single-work-main-visual {

}

.sub-single-work-main-visual__inner {
    width: 100%;
    max-width: rem(600);
    margin: 0 auto;
    
    @include mq('md') {
        max-width: rem(800);
    }
}

.sub-single-work-main-visual__primary {
    position: relative;
    margin-bottom: rem(32);
    
    @include mq('md') {
        margin-bottom: rem(40);
    }
}

.sub-single-work-main-visual__secondary {
    margin-bottom: rem(32);

    @include mq('md') {
        margin-bottom: rem(80);
    }
}

.sub-single-work-main-visual__primary-content {
    position: relative;
}

.sub-single-work-main-visual__primary-content::before {
    content: "";
    display: block;
    padding-top: 66.9333%;
    
    @include mq('md') {
        padding-top: 53.375%;
    }
}

.sub-single-work-main-visual__primary-content img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}

.swiper-button-prev.sub-single-work-main-visual__prev {
    width: rem(30);
    height: rem(30);
    background: url(/wp-content/themes/test//images/single-works/arrow-prev.png) no-repeat center center / contain;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 0;
    left: rem(8);
    
    @include mq('md') {
        width: rem(50);
        height: rem(50);
    }
}

.swiper-button-next.sub-single-work-main-visual__next {
    width: rem(30);
    height: rem(30);
    background: url(/wp-content/themes/test//images/single-works/arrow-next.png) no-repeat center center / contain;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 0;
    right: rem(8);

    @include mq('md') {
        width: rem(50);
        height: rem(50);
    }
}

.sub-single-work-main-visual__secondary-content {
    position: relative;
    // width: rem(163);
    width: 100%;
    // max-width: rem(163);
    display: inline-block;
}

.sub-single-work-main-visual__secondary-content::before {
    content: "";
    display: block;
    padding-top: 100%;
}

.sub-single-work-main-visual__secondary-content img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}

.sub-single-work-main-visual__secondary .swiper-slide {
    width: rem(163);
    
    @include mq('md') {
        width: rem(93);
    }
}