@use "global" as *;

.sub-single-heading {
    color: $white;
}

.sub-single-heading__inner {
    width: 100%;
    max-width: rem(600);
    margin: 0 auto rem(16);
    padding-left: rem(20);
    
    @include mq('md') {
        max-width: rem(800);
        padding-left: 0;
        margin-bottom: rem(32);
    }
}

.sub-single-heading__title {
    font-size: rem(20);
    font-weight: normal;
    margin-bottom: rem(16);
    letter-spacing: 0.02em;
    line-height: 1.75;
    
    @include mq('md') {
        font-size: rem(23);
        margin-bottom: rem(32);        
        line-height: 1.521739;
    }
}

.sub-single-heading__index {

}

.sub-single-heading__time {
    letter-spacing: 0.025em;
    margin-right: rem(30);
}

.sub-single-heading__category {
    letter-spacing: 0.125em;
    display: inline-block;
    padding: rem(8) rem(20);
    border: 1px solid $white;
}