@use "global" as *;

.sub-company-map {

}

.sub-company-map__inner {
    width: 100%;
    max-width: rem(600);
    margin: 0 auto;

    @include mq('md') {
        max-width: rem(1100);
    }
}

.sub-company-map__wrapper {
    width: 100%;
    padding-top: 80%;
    position: relative;
    
    @include mq('md') {
        padding-top: 33.36%;
    }
}

.sub-company-map__wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}