@use "global" as *;

.sub-content-card {

    @include mq('md') {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;        
    }
    
}

.sub-content-card:nth-child(even) {

    @include mq('md') {
        flex-direction: row-reverse;
    }
}

.sub-content-card__image {
    margin-bottom: rem(24);
    position: relative;
    width: 100%;
    
    @include mq('md') {
        flex-shrink: 0;
        width: rem(550);        
    }
    
}

.sub-content-card__body {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
    
    @include mq('md') {
        width: rem(550);
        padding-right: 0;
        padding-left: 0;
        margin-top: rem(10);        
    }
}

.sub-content-card__title {
    color: $white;
    font-size: rem(20);
    letter-spacing: 0.1em;
    margin-bottom: rem(24);
    text-align: center;

    @include mq('md') {
        text-align-last: left;        
    }
}

.sub-content-card__text {
    color: $white;
    letter-spacing: 0.125em;
    
}


.sub-content-card__image::before {
    content: "";
    display: block;
    padding-top: 69.78%;

    @include mq('md') {
        padding-top: 66.72%;
    }
}

.sub-content-card__image img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}