@use "global" as *;

.sub-single-blog-content {
    color: $white;
}

.sub-single-blog-content__inner {
    width: 100%;
    max-width: rem(600);
    margin: 0 auto;
    
    @include mq('md') {
        max-width: rem(800);
    }
}


.sub-single-blog-content__image {
    position: relative;
    margin-bottom: rem(24);
    
    @include mq('md') {
        margin-bottom: rem(32);
    }
}

.sub-single-blog-content__image::before {
    content: "";
    display: block;
    padding-top: 66.93333%;
}

.sub-single-blog-content__image img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}


.sub-single-blog-content__text {
    padding: 0 rem(20);
    letter-spacing: 0.025em;
    line-height: 2.1875;
    margin-bottom: rem(24);
    
    @include mq('md') {
        padding: 0;
        margin-bottom: rem(32);
    }
}

.sub-single-blog-content__text:last-of-type {
    margin-bottom: rem(80);
    
    @include mq('md') {
        margin-bottom: rem(104);
    }
}

.sub-single-blog-content__title-second {
    padding: 0 rem(29);
    font-size: rem(20);
    font-weight: normal;
    letter-spacing: 0.1em;
    position: relative;
    margin-bottom: rem(24);
    
    @include mq('md') {
        padding: 0 rem(8);
        margin-bottom: rem(32);
    }
}

.sub-single-blog-content__title-second::before {
    content: "";
    position: absolute;
    height: rem(29);
    width: 3px;
    background: $white;
    left: 5%;
    
    @include mq('md') {
        left: 0;
    }
}

.sub-single-blog-content__title-third {
    // padding: 0 rem(29);
    margin-left: rem(29);
    margin-bottom: rem(24);
    font-size: rem(20);
    font-weight: normal;
    letter-spacing: 0.1em;
    position: relative;
    display: inline-block;
    border-bottom: 3px solid $white;
    
    @include mq('md') {
        margin-left: rem(7);
        margin-bottom: rem(32);
    }
}

.sub-single-blog-content__title-third::before {
    // content: "";
    // position: absolute;
    // height: 3px;
    // width: calc(100% - rem(29));
    // background: $white;
    // bottom: 0;
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px;
    width: rem(73);
    height: inherit;
    border-bottom: 3px solid $yellow;
}

.sub-single-blog-content__pagenation {

}

.sub-single-blog-content__pagenation {
    padding: 0 rem(13);
}

.sub-single-blog-content__pagenation .wp-pagenavi {
    text-align: center;
    width: rem(310);
    margin: 0 auto;
    display: flex;
    // justify-content: space-between;
}

.sub-single-blog-content__pagenation .previouspostslink {
    margin: 0;
}

.sub-single-blog-content__pagenation.pagenation .wp-pagenavi .nextpostslink {
    margin: 0;
}

.sub-single-blog-content__pagenation.pagenation .page.smaller {
    margin: 0;
}
