@use "global" as *;

.sub-404-content {
    color: $white;
}

.sub-404-content__title {
    color: $yellow;
    font-size: rem(40);
    letter-spacing: 0.05em;
    line-height: 0.875;
    font-weight: normal;
    text-align: center;
    opacity: 0.2;
    margin-bottom: rem(80);
    
    @include mq('md') {
        font-size: rem(60);
        letter-spacing: 0.0333em;
        line-height: 0.5833;
    }
}

.sub-404-content__text {
    font-size: rem(20);
    text-align: center;
    margin-bottom: rem(80);
    letter-spacing: 0.1em;
    font-weight: normal;
    
    @include mq('md') {
        font-size: rem(30);
        letter-spacing: 0.0666em;
        line-height: 1.1666;
    }

}

.sub-404-content__button {
    text-align: center;
}