@use "global" as *;

.top-announce {
    position: relative;
    z-index: map-get($layer, "default" ) + 1;
}

.top-announce__inner {
    width: 100%;
        max-width: rem(600);
        padding-right: $padding-sp;
        padding-left: $padding-sp;
        margin-right: auto;
        margin-left: auto;
        
        @include mq("md") {
            // max-width: rem(1042) + strip-unit($padding-pc) * 2;
            max-width: rem(1092);
            padding-right: $padding-pc;
            padding-left: $padding-pc;
        }
}