@use "global" as *;


.drawer-content {
    color: $white;
    text-align: center;
    background: $black;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    // width: 100%;
    // height: 100%;
    z-index: map-get($layer, "drawer" );
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease;
    width: 100%;
}

.drawer-content.is-active {
    visibility: visible;
    opacity: 1;
}

.drawer-content__wrapper {
    width: 100%;
    height: 100%;
    // display: flex;
    // justify-content: center;
    // align-items: center;
}

.drawer-content__items {
    
}
.drawer-content__items a {
    display: block;   
}

.drawer-content__item {

}

.drawer-content__item:not(:first-child) {
    margin-top: rem(32);
}

