@use "global" as *;

.contact {
    background: $gray;
    padding: rem(19) 0 rem(64);
    text-align: center;
    position: relative;
    z-index: map-get($layer, "default" );
    
    @include mq('md') {
        padding: rem(57) 0 rem(149);
    }

}


.contact__titles {
    margin-bottom: rem(43);
        
    @include mq('md') {
        margin-bottom: rem(-13);

    }
}


.contact__main-title.top-titles__main {
    @include mq('md') {
        top: rem(90);
    }
}


.contact__secondary-title {

}

.contact__text {
    color: $white;
    letter-spacing: 0.125em;
    line-height: 1.43;
    margin-bottom: rem(47);
    text-align: left;
        
    @include mq('md') {
        text-align: center;   
        margin-bottom: rem(60);
    }
}

.contact__button {
    @include mq('md') {
        display: inline-block;
        padding: rem(14) rem(104) rem(14) rem(96);
    }
}