@use "global" as *;

.breadcrumb {

}

.breadcrumb__inner {
    width: 100%;
    max-width: rem(600);
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    
    @include mq("md") {
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.breadcrumb__text {
    color: $white;
    letter-spacing: 0.06em;
    font-size: rem(16);
    font-weight: $regular;
}