@use "global" as *;

.header {
    height: rem(50);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: rgba(17,17,17, 0.2);
    transition: all .3s ease;
    
    @include mq("md") {
        padding: 0 0;
    }
    
}

.header.header--background-black {
    background: $black;
}

.header.js-background-change {
    background: $black;
}

.header__inner {
    height: inherit;
    padding: rem(2) rem(25) 0 rem(20);
    width: inherit;
    margin: 0 auto;
    display: flex;
    align-items: center;

    @include mq("md") {
        width: inherit;
        padding: 0 0 0 rem(32);
        align-items: center;
    }
}

.header__logo {
    width: rem(70);
    margin-right: auto;
        
    @include mq("md") {
        width: rem(94);
    }
}

