@use "global" as *;

.wp-pagenavi {
    text-align: center;
    color: $white;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    

    @include mq('md') {
        display: block;
    }
}

.wp-pagenavi a {
    border: 1px solid white;
    padding: rem(0) rem(7) rem(0);
}

.wp-pagenavi span {
    border: 1px solid white;
    padding: rem(0) rem(7) rem(0);
}

// .wp-pagenavi a:nth-child( n + 2):nth-child( -n + 4) {
//     margin-right: rem(12);
// }

// .wp-pagenavi span:nth-child( n + 2):nth-child( -n + 4) {
//     margin-right: rem(12);
// }

.wp-pagenavi .page.smaller {
    margin-right: rem(12);
    margin-top: rem(10);
    
    @include mq('md') {
        margin-top: rem(0);
        
    }
}

.wp-pagenavi .page.larger {
    margin-left: rem(12);
    margin-top: rem(10);
    
    @include mq('md') {
        margin-top: rem(0);
        
    }
}

.wp-pagenavi .current {
    margin-top: rem(10);
    
    @include mq('md') {
        margin-top: rem(0);
        
    }
}

.wp-pagenavi a:hover {
    background: yellow;
    color: $black;
}

.wp-pagenavi span.current {
    background: $white;
    color: $black;
}

.wp-pagenavi .previouspostslink,.nextpostslink {
    border: 1px solid white;
    padding: rem(0) rem(11);
    letter-spacing: 0.125em;
}

.wp-pagenavi .previouspostslink {
    margin-right: rem(20);
    margin-left: rem(2);

    margin-top: rem(10);
    
    @include mq('md') {
        margin-top: rem(0);
        
    }
}

.wp-pagenavi .nextpostslink {
    margin-left: rem(17);

    margin-top: rem(10);
    
    @include mq('md') {
        margin-top: rem(0);
        
    }
}

