@use "global" as *;

.sub-single-card {
    display: inline-block;

    @include mq('md') {
        display: flex;
        flex-direction: column;
    }
}

.sub-single-card__image {
    position: relative;
}

.sub-single-card__image::before {
    content: "";
    display: block;
    padding-top: 66.56%;

    @include mq('md') {

        &::before {
            padding-top: 65.57%;

        }
    }
}

.sub-single-card__image img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}

.sub-single-card__body {
    background: $white;
    padding: rem(5) rem(22) rem(17) rem(21);
    transition: all .3s;
        
    @include mq('md') {
        padding: rem(3) rem(8) rem(14);
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }
}

.sub-single-card__title {
    font-size: rem(18);
    line-height: 1.94;
    letter-spacing: 0.06em;
    margin-bottom: rem(4);
    transition: all .3s;
        
    @include mq('md') {
        margin-bottom: rem(6);
        line-height: 2.5;
    }
}

.sub-single-card__excerpt {
    font-size: rem(14);
    font-weight: $regular;
    line-height: 1.85;
    letter-spacing: 0.08em;
    margin-bottom: rem(7);
    transition: all .3s;
        
    @include mq('md') {
        margin-bottom: rem(11);
    }
}

.sub-single-card__headding {
    display: flex;
    align-items: center;

    @include mq('md') {
        margin-top: auto;
    }
}

.sub-single-card__category {
    font-size: rem(14);
    letter-spacing: 0.07em;
    background: $yellow;
    padding: rem(1) rem(5);
    margin-right: auto;
}

.sub-single-card__time {
    font-size: rem(14);
    letter-spacing: 0.07em;
    transition: all .3s;
}

.sub-single-card:hover {
    opacity: 1;
}

.sub-single-card:hover .sub-single-card__body {
    background: $gray;
}

.sub-single-card:hover .sub-single-card__title {
    color: $white;
}

.sub-single-card:hover .sub-single-card__excerpt {
    color: $white;
}

.sub-single-card:hover .sub-single-card__time {
    color: $white;
}
