@use "global" as *;

.top-common {
    color: $white;
}

.top-common__title {
    font-size: rem(20);
    letter-spacing: 0.1em;
    text-align: center;
    margin-bottom: rem(24);
    
    @include mq('md') {
        font-size: rem(25);
        letter-spacing: 0.02em;
        text-align: left;
        margin-bottom: rem(35);

    }
}

.top-common__text {
    letter-spacing: 0.125em;
    margin-bottom: rem(26);
    line-height: 1.44;

    @include mq('md') {
        letter-spacing: 0.03em;
        line-height: 2.19;
        margin-bottom: rem(48);
    }
}

.top-common__button {

}