@use "global" as *;

.nav {
    color: $white;
    height: inherit;
}

.nav__lists {
    height: inherit;

    @include mq('md') {
        display: flex;
        align-items: center;
    }
}

.nav__list {
    height: inherit;
}

.nav__list a {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 rem(15) 0 rem(16);
    letter-spacing: 0.125em;
    position: relative;
}

