.nav__list a::after {
    content: "";
    height: rem(1);
    width: calc( 100% - rem(31));
    background: $yellow;
    position: absolute;
    bottom: rem(8);
    left: 50%;
    transform: scaleX(0) translateX(-50%);
    transform-origin: left top;
    transition: all .3s;
}


