//         padding-right: 0;
//     }
// }

.nav__link--last-child::after {
    transform: scaleX(0) translateX(-44%);
    width: calc( 100% - rem(14));
}

.nav__link--last-child:hover::after {
    transform: scaleX(1) translateX(-44%);
}

.nav__list.nav__list--footer a {
    padding: rem(3) rem(14) rem(4) rem(14);
        
    @include mq('md') {
        padding: rem(14) rem(14) rem(14) rem(14);
    }
}

