.announce__wrapper {
    @include mq('md') {
        display: flex;
        align-items: flex-start;
        justify-content: center;
    }
}

.announce__headding {
    display: flex;
    align-items: center;
    margin-bottom: rem(14);
        
    @include mq('md') {
        margin-bottom: 0;
        flex-shrink: 0;
    }
}

.announce__date {
    margin-right: rem(32);
        
    @include mq('md') {
        margin-right: rem(47);
    }
}

.announce__category {
    display: inline-block;
    padding: rem(5) rem(20) rem(3);
    margin-top: rem(1);
    border: rem(1) solid $white;
    font-size: rem(14);
        
    @include mq('md') {
        padding: rem(6) rem(21) rem(4) rem(19);
        margin-right: rem(62);
    }
}

.announce__title {
    display: inline-block;
    margin-bottom: rem(28);

    @include mq('md') {
        margin-bottom: 0;
        margin-right: auto;
        padding-right: rem(48);
        padding-top: rem(5);
    }
}

