@use "global" as *;

.sub-main-visual {
    position: relative;
}

.sub-main-visual__image {
    width: 100%;
    position: relative;
}

.sub-main-visual__image::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(#000000, 0.2);
}

.sub-main-visual__title {
    color: $white;
    font-size: rem(30);
    letter-spacing: 0.07em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    text-align: center;

    @include mq('md') {
        font-size: rem(35);
    }

}

.sub-main-visual__title.sub-main-visual__title--works {
    letter-spacing: 0.02em;
    top: 47%;
            
    @include mq('md') {
        top: 48%;
    }
}

