@use "global" as *;

.sub-single-work-content {
    color: $white;
}

.sub-single-work-content__inner {
    width: 100%;
    max-width: rem(600);
    padding: 0 rem(20);
    margin: 0 auto;
    
    @include mq('md') {
        max-width: rem(800);
        padding: 0;
        
    }
}

.sub-single-work-content__items {
    margin-bottom: rem(80);

    @include mq('md') {
        margin-bottom: rem(104);
    }
}

.sub-single-work-content__item {
}

.sub-single-work-content__item:not(:last-child) {
    margin-bottom: rem(32);
    
    @include mq('md') {
        margin-bottom: rem(80);
    }
}

.sub-single-work-content__pagenation {
    padding: 0 rem(13);
}

.sub-single-work-content__pagenation .wp-pagenavi {
    text-align: center;
    width: rem(310);
    margin: 0 auto;
    display: flex;
    // justify-content: space-between;
}

.sub-single-work-content__pagenation .previouspostslink {
    margin: 0;
}

.sub-single-work-content__pagenation.pagenation .wp-pagenavi .nextpostslink {
    margin: 0;
}

.sub-single-work-content__pagenation.pagenation .page.smaller {
    margin: 0;
}

.wp-pagenavi__wrapper {
    display: flex;
}  

.wp-pagenavi__items {
    width: 33.3333%;
}

