@use "global" as *;

.footer {
    text-align: center;
    color: $white;
    padding: rem(52) 0;
    
    @include mq('md') {
        padding: rem(25) 0;
    }
}

.footer__inner {
    @include mq('md') {
        display: flex;
        box-sizing: border-box;
        width: rem(1280);
        max-width: 100%;
        margin: 0 auto;
        padding-right: rem(32);
        padding-left: rem(32);
        margin-bottom: rem(36);
    }
}

.footer__logo {
    width: rem(70);
    margin: 0 auto;
    margin-bottom: rem(7);

    @include mq('md') {
        margin: 0 auto 0 0;
        padding-top: rem(12);
    }
}

.footer__menu {
    margin-bottom: rem(27);
    
    @include mq('md') {
        margin-bottom: 0;
    }
}

.footer__lists {
    margin-bottom: rem(27);
        
    @include mq('md') {
        display: flex;
    }
}

.footer__copy {
    display: block;
    font-size: rem(11);
    letter-spacing: 0.109em;

    @include mq('md') {
        font-size: rem(15);
        letter-spacing: 0.08em;
    }
}