@use "global" as *;

.works {
    position: relative;
    z-index: map-get($layer , "default");
}


.works::before {
    content: "";
    width: 70vw;
    height: calc(100% - rem(131));
    background: $gray;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: map-get($layer , "default");
}

.works__inner {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    z-index: map-get($layer , "default");

    @include mq('md') {
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.works__content-wrapper {
        
    @include mq('md') {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: rem(81) 0 rem(44);
        position: relative;
        z-index: map-get($layer , "default");
    }
}

.works__titles {
    margin-bottom: rem(79);
    
    @include mq('md') {
        margin-bottom: rem(-35);
    }
}

.works__main-title {
        
    @include mq('md') {
        letter-spacing: 0.01em;
        top: rem(50);
    }
}

.works__secondary-title {

    @include mq('md') {
    }
}

.works__images {
    background: $gray;
    margin-bottom: rem(-8);

    @include mq('md') {
        width: rem(557);
        background: none;
        margin-right: rem(32);
    }
}

.works__image-wrapper {
    position: relative;
    width: 100%;
}

.works__image-wrapper::before {
    content: "";
    display: block;
    padding-top: 67.6%;

    @include mq('md') {
        padding-top: 69.8%;
    }
}

.works__image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.works__image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.works__body {
    background: $gray;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    padding-bottom: rem(20);
    
    @include mq('md') {
        background: none;
        padding-right: 0;
        padding-left: 0;
        padding-bottom: rem(46);
        z-index: map-get($layer , "default");
    }
}

.works__diagonal-line-first {
    height: rem(1);
    width: 206vw;
    background: $yellow;
    z-index: map-get($layer, "default") - 2;
    transform: rotate(61deg);
    transform-origin: right top;
    position: absolute;
    right: rem(1);
    bottom: 77.5%;
    
    
    @include mq('md') {
        width: 143vw;
        bottom: 9%;
        right: rem(1);
        transform: rotate(45deg);
    }
}

.works__diagonal-line-second {
    height: rem(1);
    width: 386vw;
    background: $yellow;
    z-index: map-get($layer, "default") - 1;
    transform: rotate(-75deg);
    transform-origin: right top;
    position: absolute;
    right: rem(1);
    bottom: 72%;

    @include mq('md') {
        width: 143vw;
        bottom: 10%;
        right: rem(1);
        transform: rotate(-45deg);
    }
}

.works .swiper-container {
    padding-bottom: rem(31);
    
    @include mq('md') {
        padding-bottom: rem(42);
    }
}

.works .swiper-pagination-bullet {
    background: #c4c4c4;
}

.works .swiper-container-horizontal>.swiper-pagination-bullets {
    margin: 0 rem(6);
}

.works .swiper-container-horizontal>.swiper-pagination-bullet {
    margin: 0 rem(6);
}

.works .swiper-pagination-bullet-active {
    background: $yellow;
}

