@use "global" as *;

.top-titles {
    color: $white;
    text-align: center;
    position: relative;

    @include mq('md') {
        position: relative;
    }
}

.top-titles__main {
    font-size: rem(20);
    letter-spacing: 0.1em;
    position: absolute;
    left: 50%;
    top: 66%;
    transform: translateX(-50%);
    
    @include mq('md') {
        font-size: rem(30);
        letter-spacing: 0.07em;
        top: 24%;
    }
}

.top-titles__main.top-titles__main--contact {
    top: 62%;

    @include mq('md') {
        top: 52%;
    }
}

.top-titles__secondary {
    font-size: rem(50);
    font-weight: $extra-light;
    letter-spacing: 0.04em;
    color: $yellow;
    font-family: $second-font-family;
    margin-bottom: rem(-25);
    opacity: 0.2;
    
    @include mq('md') {
        font-size: rem(110);
        letter-spacing: 0.02em;
        text-align: left;
        margin-bottom: rem(0);
    }
}

.top-titles__secondary--text-align-right {
    @include mq('md') {
        text-align: right;
    }
}