@use "global" as *;

.blog {
    position: relative;
    z-index: map-get($layer, "default" );
}

.blog__titles {
    margin-bottom: rem(95);
        
    @include mq('md') {
        margin-bottom: rem(-25);
    }
}

.blog__main-title.top-titles__main {
    @include mq('md') {
        top: rem(60);
    }
}

.blog__secondary-title {

}

.blog__cards {
    margin-bottom: rem(7);

    @include mq('md') {
        position: relative;
        z-index: map-get($layer, "default" );
        margin-bottom: rem(49);
    }
}

.blog__button {
    @include mq('md') {
        text-align: center;
    }
}