@use "global" as *;

.blog-card {
    display: inline-block;

    @include mq('md') {
        display: flex;
        flex-direction: column;
    }
}

.blog-card__image {
    position: relative;
}

.blog-card__image::before {
    content: "";
    display: block;
    padding-top: 66.56%;

    @include mq('md') {

        &::before {
            padding-top: 65.57%;

        }
    }
}

.blog-card__image img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
}

.blog-card__body {
    background: $white;
    padding: rem(5) rem(22) rem(17) rem(21);
    transition: all .3s;
        
    @include mq('md') {
        padding: rem(5) rem(22) rem(17) rem(22);
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }
}

.blog-card__title {
    font-size: rem(18);
    line-height: 1.94;
    letter-spacing: 0.06em;
    margin-bottom: rem(4);
    transition: all .3s;
        
    @include mq('md') {
        margin-bottom: rem(6);
    }
}

.blog-card__excerpt {
    font-size: rem(14);
    font-weight: $regular;
    line-height: 1.85;
    letter-spacing: 0.08em;
    margin-bottom: rem(7);
    transition: all .3s;
        
    @include mq('md') {
        margin-bottom: rem(11);
    }
}

.blog-card__headding {
    display: flex;
    align-items: center;

    @include mq('md') {
        margin-top: auto;
    }
}

.blog-card__category {
    font-size: rem(14);
    letter-spacing: 0.07em;
    background: $yellow;
    padding: rem(1) rem(5);
    margin-right: auto;
}

.blog-card__time {
    font-size: rem(14);
    letter-spacing: 0.07em;
    transition: all .3s;
}

.blog-card--new {
    position: relative;
}

.blog-card--new::before {
    position: absolute;
    content: "New";
    letter-spacing: 0.14em;
    width: rem(60);
    height: rem(60);
    border-radius: 50%;
    background: $orange;
    text-align: center;
    padding: rem(17) 0 0 rem(2);
    top: 0;
    left: 0;
    transform: translate(-27%,-28%);
    z-index: map-get($layer , "default");
}

.blog-card:hover {
    opacity: 1;
}

.blog-card:hover .blog-card__body {
    background: $gray;
}

.blog-card:hover .blog-card__title {
    color: $white;
}

.blog-card:hover .blog-card__excerpt {
    color: $white;
}

.blog-card:hover .blog-card__time {
    color: $white;
}

