@use "global" as *;

.sub-content-cards {

}

.sub-content-cards__inner {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-right: auto;
    margin-left: auto;

    @include mq('md') {
        padding-left: $padding-pc;
    }
}

.sub-content-cards__item {
    
}

.sub-content-cards__item:not(:last-child) {
    margin-bottom: rem(48);

    @include mq('md') {
        margin-bottom: rem(104);
    }
}



