@use "global" as *;

.blog-cards {

    @include mq('md') {
        display: flex;
        flex-wrap: wrap;
    }

}


.blog-cards__item {
    margin-bottom: rem(16);
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    @include mq('md') {
        width: calc( 33.3333% - rem(25) * 2 / 3);
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    }
}


.blog-cards__item:last-child {
    margin-bottom: 0;
}

.blog-cards__item:not(:nth-child( 3n )) {
    @include mq('md') {
        margin-right: rem(25);
    }
}

.blog-cards__item:not(:nth-last-child(-n + 3)) {
    @include mq('md') {
        margin-bottom: rem(50);
    }
}

