@use "global" as *;

.content {
    position: relative;
    z-index: map-get($layer , "default") + 1;
}


.content__titles {
    position: relative;
    margin-bottom: rem(80);
    
    @include mq('md') {
        margin-bottom: rem(-37);
    }
}

.content__main-title.top-titles__main {

    // @include mq('md') {
    //     top: rem(42);
    // }
}

.content__secondary-title {

}

.content__items {
    display: flex;
    flex-wrap: wrap;
    position: relative;

    @include mq('md') {
    }
}

.content__item {
    display: inline-block;
    width: 50%;
    position: relative;

    @include mq('md') {
        width: 25%;
    }
}

.content__item::before {
    content: "";
    display: block;
    padding-top: 87%;

    @include mq('md') {
        padding-top: 100%;
    }
}

.content__item:hover::before {
    @include mq('md') {
        background: $white;
        opacity: 0.6;
    }
}

.content__image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.content__image img {
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.content__image::before {
    @include mq('md') {
        content: "";
        width: 100%;
        height: 100%;
        display: block;
        background: $white;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        transition: all .3s ease;
    }
}

.content__image:hover::before {
    @include mq('md') {
        opacity: 0.6;
    }
}

.content__name {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: rem(20);
    color: $white;
    letter-spacing: 0.1em;
        
    @include mq('md') {
        font-size: rem(25);
        letter-spacing: 0.08em;
    }
}

.content__diagonal-line {
    height: rem(1);
    width: 206%;
    background: $yellow;
    transform: rotate(61deg);
    transform-origin: 0% 0%;
    position: absolute;
    top: 4%;
    left: 0%;
    z-index: map-get($layer, "default" ) - 1;
    
    @include mq('md') {
        width: 140vw;
        transform: rotate(45deg);
        top: -14%;
        
    }
}