@use "global" as *;

.sub-works-cards {

    @include mq('md') {
        display: flex;
        flex-wrap: wrap;
    }
}


.sub-works-cards__item {
    @include mq('md') {
        width: calc( 50% - rem(48) / 2);
    }
}


.sub-works-cards__item:not(:first-child) {
    margin-top: rem(50);
    @include mq('md') {
        margin-top: rem(0);
    }
    
}

.sub-works-cards__item:nth-child(2n) {

    @include mq('md') {
        margin-left: rem(48);
    }
}

.sub-works-cards__item:nth-child(n + 3) {

    @include mq('md') {
        margin-top: rem(115);
    }
}