@use "global" as *;

.sub-works-card {
    display: block;
}

.sub-works-card__wrapper {
    position: relative;
    margin-bottom: rem(27);
    
    @include mq('md') {
        margin-bottom: rem(15);
    }
}

.sub-works-card__thumbnail {
    width: 100%;
    position: relative;
}

.sub-works-card__thumbnail::before {
    content: "";
    display: block;
    padding-top: 66.4%;
}

.sub-works-card__thumbnail img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.sub-works-card__category {
    color: $white;
    background: $black;
    letter-spacing: 0.125em;
    padding: rem(8) rem(19) rem(9);
    position: absolute;
    top: rem(-1);
    right: 0;

}


.sub-works-card__title {
    color: $white;
    font-size: rem(20);
    letter-spacing: 0.1em;
    text-align: center;
}




