@use "global" as *;

.sub-company-content {
    color: $white;
}

.sub-company-content__raw {
    padding: rem(18) 0 rem(18);
    border-bottom: 1px solid $white;
    font-size: rem(13);
    line-height: 1.84615;
    display: flex;
}

.sub-company-content__raw dt,dd {

}

.sub-company-content__raw dt {
    width: rem(110);
    max-width: 100%;
    flex-shrink: 0;
    flex-grow: 0;
}

.sub-company-content__raw dd {
    flex-shrink: 1;
    flex-grow: 1;
}